import uniGrid from "../../components/uni-grid/uni-grid.vue"
import uniGridItem from "../../components/uni-grid-item/uni-grid-item.vue"
export default {
	data() {
		return {
			title: 'Hello',
			array: [
				{  thumb_path: "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1596021835952&di=96e430fcf0a670288372445a77023de6&imgtype=0&src=http%3A%2F%2Fbig5.wallcoo.com%2Fcartoon%2FKitsunenoir_Design_Illustration_V%2Fwallpapers%2F2560x1440%2Fbroken-social-scene-alex-westagate.jpg",html_path: "https://10.104.17.222/storage/h5/template/1/0/125/index.html"}
			],
			isActive: 1,
			isDirection: 0,
			toggleActive: false,
			industry: [
				{label:'餐饮', value:1},
				{label:'酒店', value:2},
				{label:'商场', value:3},
				{label:'美容美发', value:4},
				{label:'服饰服装', value:5},
				{label:'教育培训', value:6},
				{label:'医疗机构', value:7},
				{label:'房产机构', value:8},
				{label:'超市', value:9},
				{label:'金融', value:10},
				{label:'公司', value:11},
				{label:'旅游', value:12},
				{label:'通信', value:13},
				{label:'节日主题', value:14},
				{label:'案例展示', value:15},
				{label:'其他', value:16}
			]
		}
	},
	components: {uniGrid,uniGridItem},
	onLoad() {
		this.getDataList(0,1)
		// uni.reLaunch({url:"../../pages/content/index"})
	},
	onReachBottom(){  //上拉触底函数
	          // if(!this.isLoadMore){  //此处判断，上锁，防止重复请求
	          //       this.isLoadMore=true
	           
	          // }
	    },
	/**
	 *  下拉加载数据 
	 * */
	onPullDownRefresh(){
		this.getDataList(this.isDirection,this.isActive)
	 },
	methods: {
		imageError: function(e) {
			console.error('image发生error事件，携带值为' + e.detail.errMsg)
		},
		openUrl:function(id) {
		   uni.navigateTo({
				url: '/pages/customMode/index?id='+id
			})
		},
		/**
		 *  切换模板类型
		 * */
		changeClass(i) {
			this.isActive = i
			this.getDataList(this.isDirection,this.isActive)
		},
		/**
		 *  切换横竖屏
		 * */
		changeBtn(num){
			this.isDirection = num
			this.toggleActive = !this.toggleActive
			this.getDataList(this.isDirection,this.isActive)
		},
		/**
		 *  查询数据列表
		 * */
		getDataList: function(d,n) {
			this.$req({
					method:'GET',
					url:'/bank/app/h5/templates',
					data:{
						"direction": d,
						"industry": n
					}
			}).then( res => {
				this.array = res.data.message.data
			}).catch( error => {
				console.log(error)
			})
		}
		
	}
}
